.single {
  &__h1 {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 40px;
    @media #{$sp} {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }
  &__h2 {
    font-size: 22px;
    font-weight: bold;
    padding: 15px 0;
    border-bottom: 3px solid #ddd;
    margin-bottom: 30px;
    position: relative;
    @media #{$sp} {
      font-size: 18px;
      padding: 10px 0;
      border-bottom: 2px solid #ddd;
      margin-bottom: 15px;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 50px;
      height: 3px;
      background: #13987F;
      z-index: 2;
      @media #{$sp} {
        bottom: -2px;
        width: 30px;
        height: 2px;
      }
    }
  }
  &__h3 {
    font-size: 20px;
    font-weight: bold;
    padding-left: 30px;
    border-left: 4px solid #13987F;
    line-height: 1.5;
    position: relative;
    margin-bottom: 30px;
    @media #{$sp} {
      font-size: 16px;
      padding-top: 5px;
      margin-bottom: 15px;
    }
  }
  &__lead {
    margin-bottom: 50px;
    @media #{$sp} {
      margin-bottom: 30px;
    }
  }
  &__time {
    margin-bottom: 30px;
    @media #{$sp} {
      margin-bottom: 15px;
    }
  }
  &__toc {
    border-top: 5px solid #13987F;
    background: #F7F7F7;
    padding: 40px;
    margin-bottom: 60px;
    @media #{$sp} {
      padding: 15px;
      margin-bottom: 30px;
    }
    &--ttl {
      font-size: 20px;
      font-weight: bold;
      color: #13987F;
      margin-bottom: 20px;
      padding-left: 35px;
      position: relative;
      @media #{$sp} {
        font-size: 16px;
        margin-bottom: 15px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 24px;
        height: 24px;
        background: url("../img/common/toc.svg") 50% / contain no-repeat;
      }
    }
    dl {
      a {
        color: #333333;
        text-decoration: underline;
        &:hover {
          opacity: 1;
          color: #0D5BAB;
          text-decoration: none;
        }
      }
      dt {
        font-weight: bold;
        margin-bottom: 10px;
      }
      dd {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        ul {
          padding-left: 1.5em;
          margin-left: 1em;
          li {
            list-style: disc;
            &:not(:last-child) {
              margin-bottom: 5px;
            }
            a {
              display: inline;
            }
          }
        }
      }
    }
    
  }
  &__txt {
    margin-bottom: 30px;
    @media #{$sp} {
      margin-bottom: 15px;
    }
    p {
      &:not(:last-child) {
        margin-bottom: 30px;
        @media #{$sp} {
          margin-bottom: 15px;
        }
      }
      img {
        height: auto;
        max-width: 100%;
        width: auto;
        display: block;
        margin: auto;
      }
    }
  }
  &__img {
    margin-bottom: 30px;
    @media #{$sp} {
      margin-bottom: 15px;
    }
    // img {
    //   max-width: none;
    //   width: 50%;
    //   @media #{$sp} {
    //     width: 100%;
    //   }
    //   &.full {
    //     width: 100%;
    //   }
    // }
  }
  &__ttl {
    color: #13987F;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    padding: 10px;
    background: #E6F2F0;
    position: relative;
    @media #{$sp} {
      font-size: 16px;
    }
    // span {
    //   position: relative;
    //   &::before {
    //     content: "";
    //     position: absolute;
    //     top: 0;
    //     bottom: 0;
    //     left: -40px;
    //     margin: auto;
    //     width: 20px;
    //     height: 20px;
    //   }
    //   &.doc {
    //     &::before {
    //       background: url("../img/common/icon-doc.svg") 50% / contain no-repeat;
    //     }
    //   }
    //   &.pen {
    //     &::before {
    //       background: url("../img/common/icon-pen.svg") 50% / contain no-repeat;
    //     }
    //   }
    // }
  }
}

.news-single {
  .containers {
    max-width: 880px;
  }
  &__date {
    margin-bottom: 20px;
    @media #{$sp} {
      margin-bottom: 15px;
    }
    .cat {
      border: 1px solid #13987F;
      background: #fff;
      color: #13987F;
    }
  
    .time {
      font-size: 16px;
      font-weight: normal;
      color: rgba(33,37,41,0.58);
      @media #{$sp} {
        font-size: 12px;
      }
    }
  }

  &__h1 {
    font-size: 32px;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: 30px;
    @media #{$sp} {
      font-size: 20px;
      margin-bottom: 15px;
    }
  }

  .sns {
    margin-bottom: 30px;
    @media #{$sp} {
      margin-bottom: 15px;
    }
  }

  &__lead {
    padding-bottom: 30px;
    border-bottom: 1px solid #CCCCCC;
    margin-bottom: 30px;
    @media #{$sp} {
      padding-bottom: 15px;
      margin-bottom: 15px;
    }
  }

  &__conts {
    p {
      &:not(:last-child) {
        margin-bottom: 30px;
        @media #{$sp} {
          margin-bottom: 15px;
        }
      }
    }
  }

}